* {
  box-sizing: border-box;
}
body {
  background-image: linear-gradient(to bottom right, black, rgb(5, 43, 47),rgb(6, 109, 109), rgb(1, 255, 255));
  margin: 0;
  font-family: "Poppins", sans-serif;
  color: aliceblue;
}
nav {
  height: 70px;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 5px 50px;
  box-shadow: 9px 8px 14px 6px rgba(0, 0, 0, 0.30);
}
.riview {
  color: rgba(255, 255, 255, 0.862);
}
.bold {
  font-weight: bold;
}
.nav--logo {
  max-width: 200px;
}
section {
  padding: 30px;
}
.centre {
  display: flex;
  flex-direction: column;
}
.centre-pic {
  max-width: 45%;
  max-height: 45%;
  align-self: center;
}
.centre-head {
  margin-bottom: 24px;
}

.centre-para {
  margin-top: 0;
}
main {
  width: 200px;
  font-size: 12px;
  flex: 0 0 auto;         
  display: flex;         
  flex-direction: column;
  position: relative;
}
section {
  padding: 20px;
}
.banners-list {
  display: flex;
  flex-wrap: nowrap;
  gap: 20px;
  overflow-x: auto;
}
.banner-pic {
  width: 95%;
  height: 250px; 
  border-radius: 10px;
  margin-bottom: 9px;
}

.banner-stats {
  display: flex;
  align-items: center;
}
.star {
  height: 15px;
}
.banner-title {
  overflow: hidden;
  text-overflow: ellipsis;
}
.banner-price {
  margin-top: auto;
}
.banner-para {
  display: flex;
  align-items: center;
  justify-content: center;
}
.badge {
  position: absolute;
  top: 6px;
  left: 6px;
  background-color: black;
  padding: 5px 7px;
  border-radius: 9px;
  font-weight: bold;
}